import React, { Component } from 'react';
import '../css/formulaire.css'
import Diplome from './diplome';
class Formulaire extends Component {
    
    constructor(props) {
        super(props);
        this.state={
            centre_p: "",
            Ecole: "",
            centre: "",
            Num_place: "",
            Date_nais: "",
            np_pere: "",
            np_mere: "",
            session: "",
            date_deliv: "",

        }
    };
    
    setData=(event)=>{
        this.setState({
            centre_p: event.target.value
        })
        console.log(this.state.centre_p)
      }

    //Pour donner au diplome dans Diplome le state contenant les data
    loadDiplome() {
        return <Diplome 
            diplome={this.state}
        />;
    }


    render() {
        return (
            <div className="containers">
                <div className="heading">Renseignement</div>
                <form action='#'>
                    <div className="card-details">
                        <div className="card-box">
                            <span className="details">Centre d'animation pdagogique de</span>
                            {/*Lier l'input au data cenntre_p */}<input type="text" onChange={() => this.state.centre_p} placeholder="Centre d'animation pdagogique de" />
                        </div>
                        <div className="card-box">
                            <span className="details">Ecole</span>
                            <input type="text" placeholder="Ecole" />
                        </div>
                        <div className="card-box">
                            <span className="details">Centre</span>
                            <input type="text" placeholder="Centre" />
                        </div>
                        <div className="card-box">
                            <span className="details">N place</span>
                            <input type="text" placeholder="Numro de place" />
                        </div>
                        <div className="card-box">
                            <span className="details">Date et lieu de naissance</span>
                            <input type="text" placeholder="Date et lieu de naissance" />
                        </div>
                        <div className="card-box">
                            <span className="details">Nom et prenom du pre</span>
                            <input type="text" placeholder="Entrer le nom complet du pre" />
                        </div>
                        <div className="card-box">
                            <span className="details">Nom et prenom de la mre</span>
                            <input type="text" placeholder="Entrer le nom complet de la mre" />
                        </div>
                        <div className="card-box">
                            <span className="details">Session</span>
                            <input type="text" placeholder="Session de" />
                        </div>
                        <div className="card-box">
                            <span className="details">Date de dlivrance</span>
                            <input type="text" placeholder="Date de dlivrance" />
                        </div>
                    </div>
                    <div className="button">
                        <input type="submit" value="Gnerer diplme" onClick={() => this.loadDiplome} />
                    </div>
                </form >
            </div >
        );
    }
}

export default Formulaire;